<template>
  <div class="scan-qr-view">
    <video ref="videoElem"></video>
  </div>
</template>
<script>
import QrScanner from "qr-scanner";
export default {
  name: "ScanQRView",
  data() {
    return {
      qrScanner: null,
    };
  },
  mounted() {
    console.log("mounted");
    this.qrScanner = new QrScanner(
      this.$refs.videoElem,
      (result) => {
        console.log("decoded qr code:", result);
        if (result && result.data) {
          this.$router.push({
            name: "/scan-result",
            params: {
              data: result.data,
            },
          });
        }
      },
      {
        // onDecodeError: (error) => {
        //   console.log("error:", error);
        // },
        highlightScanRegion: true,
        // highlightCodeOutline: true,
        calculateScanRegion: (v) => {
          const smallestDimension = Math.min(v.videoWidth, v.videoHeight);
          // Make scan region smaller to match better small qr codes
          const scanRegionSize = Math.round((2 / 5) * smallestDimension);
          let region = {
            x: Math.round((v.videoWidth - scanRegionSize) / 2),
            y: Math.round((v.videoHeight - scanRegionSize) / 2),
            width: scanRegionSize,
            height: scanRegionSize,
          };
          return region;
        },
      }
    );

    console.log("after new QrScanner", QrScanner.listCameras);

    // this.qrScanner.start();
    QrScanner.listCameras(true)
      .then((cameras) => {
        console.log("cameras", cameras);
        this.qrScanner
          .setCamera(
            cameras.find((camera) => camera.label.startsWith("camera2 0"))
              ?.id ?? "environment"
          )
          .then(() =>
            this.qrScanner.start().catch((err) => {
              console.log(err);
              // eslint-disable-next-line no-undef
              MYAPP.showToast("App摄像头权限未打开");
            })
          );
      })
      .catch((err) => {
        console.log(err);
        // eslint-disable-next-line no-undef
        MYAPP.showToast("App摄像头权限未打开");
      });
  },
  beforeDestroy() {
    if (this.qrScanner) {
      this.qrScanner.stop();
    }
  },
};
</script>
<style lang="less">
.scan-qr-view {
  position: relative;

  video {
    position: fixed;
    right: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
  }
}
</style>
